@font-face {
  font-family: "Averta Demo PE Cutted Demo";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.astudio__cs {
  margin: 0 14px 0 14px;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__cs {
    display: grid;
    grid-template-columns: 1fr;
  }
}
@media (min-width: 1025px) {
  .astudio__cs {
    display: grid;
    grid-template-columns: 471px 1fr;
    align-items: center;
    column-gap: 95px;
  }
}
@media (min-width: 1025px) {
  .astudio__cs {
    margin: 0 0 0 120px;
  }
  .astudio__cs-img1 {
    height: 129px;
    width: 129px;
    position: absolute;
    left: 31rem;
    z-index: -5;
    top: -21rem;
    background: url(".../../../../../images/Ellipse 85.png");
  }
}
@media (min-width: 1025px) and (min-width: 1025px) {
  .astudio__cs-img1 {
    left: 39rem;
    top: 1.1rem;
  }
}
@media (min-width: 1025px) and (min-width: 1440px) {
  .astudio__cs-img1 {
    left: 39rem;
    top: 1.1rem;
  }
}
@media (min-width: 1025px) {
  .astudio__cs-img2 {
    height: 178px;
    width: 178px;
    position: absolute;
    right: 8rem;
    z-index: -5;
    top: 28rem;
    background: url(".../../../../../images/Rectangle 23.png");
  }
}
@media (min-width: 1025px) and (min-width: 1025px) {
  .astudio__cs-img2 {
    top: 21rem;
  }
}
@media (min-width: 1025px) and (min-width: 1440px) {
  .astudio__cs-img2 {
    top: 28rem;
  }
}
@media (min-width: 1025px) {
  .astudio__cs-dotImg {
    width: 404px;
    height: 154px;
    margin: -61px 0 0 -53px;
    background: url(".../../../../../images/Dot Ornament.png") no-repeat;
  }
}
.astudio__cs-block1 h2 {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 40px;
  line-height: 49.44px;
  color: #000000;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__cs-block1 h2 {
    width: 100%;
    margin-bottom: 30px;
  }
}
@media (min-width: 1025px) {
  .astudio__cs-block1 h2 {
    width: 100%;
    margin-bottom: 40px;
  }
}
.astudio__cs-block1 p {
  font-family: "Averta Demo PE Cutted Demo";
  font-size: 16px;
  line-height: 25px;
  color: #565656;
  margin: 0;
}
.astudio__cs-block1 button {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 30px;
  background-color: #2639ed;
  border-radius: 60px;
  padding: 20px 40px;
  border: none;
}
.astudio__cs-block1 button a {
  font-family: "Averta Demo PE Cutted Demo";
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__cs-block1 button {
    margin-top: 30px;
  }
}
@media (min-width: 1025px) {
  .astudio__cs-block1 button {
    max-width: 215px;
    margin-top: 33px;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__cs-block1 {
    margin-right: 30px;
  }
}
.astudio__cs-block2-img img {
  height: 100%;
  max-height: 542px;
  width: 100%;
}
@media (min-width: 768px) and (max-width: 1024px) {
  .astudio__cs {
    display: flex;
    flex-direction: column;
  }
}